import React from "react";
import {
  TableContainer,
  Paper,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Table,
} from "@mui/material";

var fields = [
  { label: "Temperatura Mnima", value: "temp_min", pos: "C" },
  { label: "Temperatura Mxima", value: "temp_max", pos: "C" },
  { label: "Presso", value: "pressure", pos: "hPa" },
  { label: "Umidade", value: "humidity", pos: "%" },
  { label: "Visibilidade", value: "visibility", pos: " km" },
];

function TableClimate({ resultado }) {
  return (
    <>
      <TableContainer>
        <Table size="small">
          <TableHead>
            <TableRow>
              {/* <TableCell>Campo</TableCell>
            <TableCell>Valor</TableCell> */}
            </TableRow>
          </TableHead>
          <TableBody>
            {fields.map((field, index) => {
              const fieldValue = field.value
                .split(".")
                .reduce((acc, part) => acc && acc[part], resultado);
              return (
                <TableRow key={index}>
                  <TableCell>{field.label}</TableCell>
                  <TableCell>
                    {fieldValue}
                    {field.pos}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}

export default TableClimate;
