import React from "react";
import ClimateVisualizer from "../../components/ClimateVisualizer";
import NewsList from "../../components/NewsList";
import PriceVisualizer from "../../components/PriceVisualizer";
import CurrencyExchange from "../../components/CurrencyExchangeVisualizer/CurrencyExchangeVisualizer";
import { Grid } from "@mui/material";

const Dashboard = () => {
  return (
    <>
      <Grid
        container
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        rowSpacing={8}
      >
        <Grid item xs={4}>
          <CurrencyExchange currency={"USD"} />
        </Grid>
        <Grid item xs={4}>
          <CurrencyExchange currency={"EUR"} />
        </Grid>
        <Grid item xs={12}>
          <NewsList />
        </Grid>
        <Grid
          item
          container
          xs={12}
          justifyContent="center"
          alignItems="center"
        >
          <PriceVisualizer />
        </Grid>
        <Grid item xs={12}>
          <ClimateVisualizer />
        </Grid>
      </Grid>
    </>
  );
};

export default Dashboard;
