/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

const footerStyle = css`
  background-color: white;
  color: #64c832;
  padding: 20px;
  text-align: center;
  left: 0;
  bottom: 0;
  width: 100%;
`;

const Footer = () => {
  return (
    <footer css={footerStyle}>
      <p> 2024 Sicredi. Todos os direitos reservados.</p>
    </footer>
  );
};

export default Footer;
