/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

const headerStyle = css`
  background-color: white;
  color: #64c832;
  align-items: center;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  padding: 20px;
`;

const logoStyle = css`
  width: 30%;
  max-width: 200px;
`;

const Header = () => {
  return (
    <header css={headerStyle}>
      <img
        css={logoStyle}
        src="/images/logo_sicredi.png"
        alt="Logo da Sicredi"
      />
    </header>
  );
};

export default Header;
