import React, { useState, useEffect } from "react";
import {
  TableContainer,
  Paper,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Table,
} from "@mui/material";
const API_KEY = "pub_45739e3973be33d0eece3c6936c3974a8b847";

const NewsList = () => {
  const [valores, setValores] = useState([]);
  const [error, setError] = useState([]);

  useEffect(() => {
    getNews();
  }, []);

  const getNews = async () => {
    try {
      const api_values = await fetch(
        `https://newsdata.io/api/1/news?apikey=${API_KEY}&q=agroneg%C3%B3cio&country=br&language=pt`
      );

      if (api_values.ok === false) {
        throw new Error("falha na requisio");
      }

      const response = await api_values.json();
      console.log(response);
      if (Array.isArray(response["results"])) {
        setValores(response["results"]);
      } else {
        throw new Error("Na requisio no resultou em um array");
      }
    } catch (error) {
      throw new Error(error.message);
    } finally {
      setError(error);
    }

    if (error) {
      return <di> Erro ao carregar tabela </di>;
    }
  };

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Titulo</TableCell>
            <TableCell>Fonte</TableCell>
            <TableCell>Data de Publicao</TableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {Array.isArray(valores) ? (
            valores.slice(0, 10).map((item) => (
              <TableRow key={item.article_id}>
                <TableCell>
                  <a href={item.link}> {item.title} </a>{" "}
                </TableCell>
                <TableCell> {item.source_id} </TableCell>
                <TableCell> {item.pubDate} </TableCell>
              </TableRow>
            ))
          ) : (
            <TableRow>
              <TableCell> Valores Invlidos</TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default NewsList;
