import React, { useEffect, useState } from "react";
import CITIES from "./cities";
import { Autocomplete, TextField, Box, useTheme } from "@mui/material";
import TableClimate from "./components/TableClimate";

const API_KEY = "8701d60e6498662c5e13f78285a0af7e";

function ClimateVisualizer() {
  const [cidade, setCidade] = useState(CITIES[0]);
  const [resultado, setResultado] = useState("");
  const theme = useTheme();
  useEffect(() => {
    if (cidade) consultarClima();
    else setResultado("");
  }, [cidade]);

  const consultarClima = () => {
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${cidade.label}&appid=${API_KEY}`
    )
      .then((response) => response.json())
      .then((data) => {
        console.log(data.main);
        setResultado(data.main);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "flex-start",
        gap: 2,
        paddingY: 2,
        border: `2px solid ${theme.palette.secondary.main}`,
        width: "100%", // Full width of the container
        maxWidth: "100%", // Maximum width to ensure the box does not expand indefinitely
        height: 200, // Fixed height
        overflow: "hidden", // Hide overflow to maintain fixed size
      }}
    >
      <Box sx={{ width: 300, margin: 2 }}>
        <Autocomplete
          disablePortal
          id="combo-box-capital"
          options={CITIES}
          sx={{ width: "100%" }}
          renderInput={(params) => <TextField {...params} label="Capital" />}
          value={cidade}
          onChange={(_event, newValue) => {
            setCidade(newValue);
          }}
        />
      </Box>
      <Box sx={{ flexGrow: 1, overflowY: "auto", margin: 2 }}>
        {Object.keys(resultado).length > 0 && (
          <TableClimate resultado={resultado} />
        )}
      </Box>
    </Box>
  );
}

export default ClimateVisualizer;
